.login {
    width: 100vw;
    height: 100vh;
    z-index: 999;
    position: fixed;
    top: 0px;
    left: 0px;
    background-image: url("https://coolbackgrounds.io/images/backgrounds/index/sea-edge-79ab30e2.png");
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

form {
    color: red;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
}
.field {
    width: 100%;
}

.loginActions {
    width: 180px;
    display: flex;
    align-items: center;
    align-self: flex-end;
    justify-content: space-between;
}
.registerField {
	display: flex;
	margin-top: 20px;
}
.registerField p {
	width: 240px;
}
.default {
    position: absolute;
    top: 600px;
    display: flex;
    width: 300px;
    justify-content: space-around;
}